.btn {
  background-color: #333;
  border: 1px solid #fff;
  display: inline-block;
  font-size: 1.25rem;
  font-weight: 400;
  letter-spacing: 0.1em;
  line-height: 3rem;
  text-align: center;
  transition: 0.3s;
  padding-left: 0.1em;
  position: relative;
  width: 100%;
}
@media screen and (min-width: 768px) {
  .btn {
    font-size: 1rem;
    font-weight: 500;
    letter-spacing: 0.125em;
    line-height: 3.625rem;
    padding-left: 0.125em;
  }
}

.btn::before {
  content: "";
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
  background: #fff;
  transform-origin: 100% 50%;
  transform: scaleX(0);
  transition: transform ease 0.3s;
}

.btn:hover {
  color: #333;
}

.btn:hover::before {
  transform-origin: 0% 50%;
  transform: scaleX(1);
}

@media screen and (min-width: 768px) {
  .btn--pc-small {
    background-color: #fff;
